<template>
  <div class="home">
     <div class="flip-card">
       <div class="flip-card-inner" ref="card_animation">
         <div class="flip-card-front" ref="card_front">
<!--           <video autoplay muted loop>-->
<!--            <source src="../assets/Videos/4k_Header_No_Birds.mp4" type="video/mp4">-->
<!--           </video>-->
         </div>
         <div class="flip-card-back">
           <p>back</p>
         </div>
       </div>
     </div>
  </div>
</template>

<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { Observer } from "gsap/Observer";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { MotionPathPlugin } from "gsap/MotionPathPlugin";

export default {
  mounted() {
    const { card_animation } = this.$refs
    const { card_front } = this.$refs
    gsap.registerPlugin(ScrollTrigger, Observer, ScrollToPlugin, MotionPathPlugin);

    gsap.to(card_animation,{
      // scale: 1,
      // height: "100%",
      rotationY: 0,
      duration: 3,
      delay: 0,
    });
    gsap.to(card_animation,{
      width: "100%",
      xPercent: -0,
      marginTop: "0px",
      duration: 3,
      delay: 1,
    });
    gsap.to(card_front,{
      borderRadius: "0px",
      duration: 3,
      delay: 2,
    });
  }
}

</script>

<style>

.home {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 100vh;

}
video {
  position: relative;
  width: 100%;
}


.flip-card {
  display: block;
  background-color: transparent;
  width: 100%;
  height: 100%;
  margin: auto;
  perspective: 1000px;
}

/* This container is needed for the rotation animations */
.flip-card-inner {
  display: block;
  position: relative;
  /*scale: 0.1;*/
  width: 20%;
  height: 90%;
  text-align: center;
  transform-style: preserve-3d;
  transform: rotateY(90deg);
  margin: auto;
  margin-top: 10px;
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.flip-card-front {
  border-radius: 10px;
  background: url(../assets/Testimg.png);
  background-repeat: no-repeat;
  background-size: auto;
  color: black;
}

.flip-card-back {
  background-color: #dbd9d9;
  color: white;
  transform: rotateY(180deg);
}

